.tabs__content {
    animation-name: menu;
    animation-duration: 0.5s;
    animation-fill-mode: both;
}

@keyframes menu {
    0% {
        transform: scale(0.85);
    }

    100% {
        transform: scale(1);
    }
}

.title {
    font-size: clamp(1.5rem, 1vw, 2rem);
    border-bottom: 0.2rem solid #393939;
    padding-bottom: 1rem;
}

.description {
    font-size: clamp(1rem, 1vw, 1.3rem);
    text-indent: 1rem;
    line-height: 1.5rem;
    margin: 1rem 0;
}

.description::first-letter {
    font-size: clamp(1.3rem, 1vw, 1.5rem);
}

.suptitle {
    font-size: clamp(1.5rem, 1vw, 2rem);
    text-align: center;
    color: #393939;
    margin: 1rem 0;
}

.list__skills {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    font-size: clamp(1rem, 1vw, 1.3rem);
    letter-spacing: 1px;
    padding-left: 2rem;
}

.list__skills li::before {
    content: "\2014";
    margin: 0 0.5rem;
    position: absolute;
    left: 0;
}

.list__interest {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 1rem;
}

.list__interest li {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: clamp(0.7rem, 0.7vw, 1rem);
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
}

.list__interest li svg {
    width: 3rem;
    height: 3rem;
    fill: #606060;
    margin-bottom: 0.5rem;
}

.portfolio__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 1rem;
    width: 100%;
}

.portfolio__items {
    display: flex;
    margin-top: 1.5rem;
    -webkit-perspective: 50rem;
    perspective: 50rem;
}

.portfolio__link {
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    max-width: 21rem;
    min-width: 16rem;
    background: #2c2c2c;
    border-radius: 0.7rem 0;
    border: 0.2rem solid #fff;
    padding-top: 1rem;
    transition: 0.5s;
    position: relative;
}

.portfolio__link:hover {
    -webkit-transform: rotateY(11deg);
    transform: rotateY(11deg);
}

.portfolio__link:hover .portfolio__title {
    color: #606060;
}

.portfolio__link:hover .portfolio__stack-item {
    opacity: 1;
    visibility: visible;
    left: 0;
}

.portfolio__title {
    font-size: 1rem;
    font-style: italic;
    font-weight: 700;
    padding: 0.5rem 0.5rem 1rem;
}

.portfolio__stack-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 0.5rem;
    width: 100%;
    background: #2c2c2c;
    border-radius: 0.7rem 0;
    border-bottom: 3px solid #fff;
    border-top: 3px solid #fff;
    padding: 0.5rem;
    visibility: hidden;
    opacity: 0;
    transition: 0.5s;
    position: absolute;
    left: 100%;
}

.portfolio__stack-item li {
    background: #393939;
    color: #cecece;
    border-radius: 0.3rem;
    padding: 0.25rem;
}

.portfolio__suptitle {
    width: 100%;
    font-size: 1rem;
    font-weight: 700;
    text-align: center;
    background: transparent !important;
}

.portfolio__suptitle h4 {
    color: #fff;
}